"use client"

import * as React from "react"
import { ChevronsUpDown } from "lucide-react"

import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu"
import {
  SidebarMenu,
  SidebarMenuButton,
  SidebarMenuItem,
  useSidebar,
} from "@/components/ui/sidebar"
import { useLineNavigation } from "@/components/line-navigation-provider"

function getLineInitials(lineId) {
  if (!lineId) return "?"
  const trimmed = lineId.trim()
  if (!trimmed) return "?"
  return trimmed.slice(0, 2).toUpperCase()
}

export function TeamSwitcher() {
  const { isMobile } = useSidebar()
  const { availableLines, lineId, selectLine } = useLineNavigation()

  const activeLine = React.useMemo(
    () => availableLines.find((line) => line.id === lineId) ?? null,
    [availableLines, lineId]
  )

  const effectiveLine = activeLine ?? (lineId ? { id: lineId, label: lineId } : null)

  const hasLines = availableLines.length > 0
  const displayLabel = effectiveLine?.label ?? (hasLines ? "Select a line" : "No lines available")
  const displayDescription = effectiveLine
    ? `Line ID ${effectiveLine.id}`
    : hasLines
      ? "Choose a line to view data"
      : "Line data unavailable"

  return (
    <SidebarMenu>
      <SidebarMenuItem>
        <DropdownMenu>
          <DropdownMenuTrigger asChild>
            <SidebarMenuButton
              size="lg"
              className="data-[state=open]:bg-sidebar-accent data-[state=open]:text-sidebar-accent-foreground">
              <div
                className="bg-sidebar-primary text-sidebar-primary-foreground flex aspect-square size-8 items-center justify-center rounded-lg">
                <span className="text-xs font-semibold uppercase tracking-wide">
                  {getLineInitials(effectiveLine?.id)}
                </span>
              </div>
              <div className="grid flex-1 text-left text-sm leading-tight">
                <span className="truncate font-medium">{displayLabel}</span>
                <span className="truncate text-xs text-muted-foreground">{displayDescription}</span>
              </div>
              <ChevronsUpDown className="ml-auto" />
            </SidebarMenuButton>
          </DropdownMenuTrigger>
          <DropdownMenuContent
            className="w-(--radix-dropdown-menu-trigger-width) min-w-56 rounded-lg"
            align="start"
            side={isMobile ? "bottom" : "right"}
            sideOffset={4}>
            <DropdownMenuLabel className="text-muted-foreground text-xs">
              {hasLines ? "Available lines" : "No lines to select"}
            </DropdownMenuLabel>
            {hasLines ? (
              availableLines.map((line) => (
                <DropdownMenuItem
                  key={line.id}
                  className="gap-2 p-2"
                  onClick={() => selectLine(line.id)}
                  disabled={line.id === effectiveLine?.id}>
                  <div className="flex size-6 items-center justify-center rounded-md border text-xs font-semibold uppercase">
                    {getLineInitials(line.id)}
                  </div>
                  <div className="flex flex-col">
                    <span className="text-sm font-medium">{line.label}</span>
                    <span className="text-xs text-muted-foreground">Line ID {line.id}</span>
                  </div>
                </DropdownMenuItem>
              ))
            ) : (
              <DropdownMenuItem className="text-xs text-muted-foreground" disabled>
                Contact an administrator to configure line data.
              </DropdownMenuItem>
            )}
          </DropdownMenuContent>
        </DropdownMenu>
      </SidebarMenuItem>
    </SidebarMenu>
  );
}
