"use client"

import { createContext, useCallback, useContext, useMemo } from "react"
import { useParams, usePathname, useRouter } from "next/navigation"

const DEFAULT_LINE_ROUTE = "/ESOP_Dashboard/status"

const LineNavigationContext = createContext(null)

function normalizeSegments(pathname) {
  if (!pathname) return []
  return pathname
    .split("/")
    .filter(Boolean)
}

function sanitizePath(path) {
  if (!path) return ""
  return path.startsWith("/") ? path.slice(1) : path
}

export function LineNavigationProvider({ availableLines = [], children }) {
  const router = useRouter()
  const pathname = usePathname()
  const params = useParams()

  const currentLineId = typeof params?.lineId === "string" ? params.lineId : null

  const selectLine = useCallback(
    (nextLineId) => {
      if (!nextLineId) return

      if (currentLineId === nextLineId) return

      const segments = normalizeSegments(pathname)
      let nextPathname = null

      if (currentLineId && segments[0] === currentLineId) {
        const nextSegments = [...segments]
        nextSegments[0] = nextLineId
        nextPathname = `/${nextSegments.join("/")}`
      } else {
        const basePath = sanitizePath(DEFAULT_LINE_ROUTE)
        const baseSegments = basePath ? basePath.split("/") : []
        nextPathname = `/${[nextLineId, ...baseSegments].join("/")}`
      }

      router.push(nextPathname)
    },
    [currentLineId, pathname, router]
  )

  const resolveHref = useCallback(
    (path) => {
      const sanitized = sanitizePath(path)
      if (!sanitized) {
        return currentLineId ? `/${currentLineId}` : "/"
      }

      const segments = sanitized.split("/").filter(Boolean)
      if (segments.length === 0) {
        return currentLineId ? `/${currentLineId}` : "/"
      }

      if (!currentLineId) {
        return `/${segments.join("/")}`
      }

      return `/${[currentLineId, ...segments].join("/")}`
    },
    [currentLineId]
  )

  const value = useMemo(
    () => ({
      availableLines,
      lineId: currentLineId,
      selectLine,
      resolveHref,
    }),
    [availableLines, currentLineId, resolveHref, selectLine]
  )

  return (
    <LineNavigationContext.Provider value={value}>
      {children}
    </LineNavigationContext.Provider>
  )
}

export function useLineNavigation() {
  const context = useContext(LineNavigationContext)
  if (!context) {
    throw new Error("useLineNavigation must be used within a LineNavigationProvider")
  }
  return context
}
